/* Coolors Exported Palette - coolors.co/2b2d42-8d99ae-edf2f4-ef233c-d90429 */
footer {
  background-color: #2b2d42;
  position: relative; }
  footer ul {
    list-style: none;
    text-align: center;
    padding: 2em 0 0 0;
    margin-bottom: 0;
    margin-top: 0; }
  footer li {
    display: inline-block;
    margin: 0 1em; }
  footer a {
    color: #edf2f4;
    text-decoration: none; }

li:hover .fa-square {
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  color: #40e0d0; }

.fa-square {
  color: #8d99ae; }

#copyright {
  color: #edf2f4;
  margin: 0 0;
  padding-bottom: 1em;
  font-size: .7em; }

#return-to-home {
  display: block;
  margin-bottom: 2em;
  position: absolute;
  top: -25%;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
      -ms-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%); }

.far {
  font-size: 3em; }

.far:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  color: #40e0d0;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s; }



