/* Coolors Exported Palette - coolors.co/2b2d42-8d99ae-edf2f4-ef233c-d90429 */
.about {
  background-color: #2b2d42;
  color: #edf2f4;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.about-title {
  font-size: 2em;
  margin: 0em 0em 1em 0em;
  padding-top: 2em;
  font-weight: bold; }
  @media screen and (max-width: 768px) {
    .about-title {
      padding-top: 1em; } }

.about-body {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1em; }
  @media screen and (max-width: 500px) {
    .about-body {
      grid-template-columns: 1fr; } }

.intro {
  width: 70%;
  margin: 0 auto; }
  @media screen and (max-width: 500px) {
    .intro {
      width: 85%; } }

.portrait {
  width: 7em;
  height: 7em;
  border-radius: 50%;
  border: 1px solid white;
  margin: 0 auto;
  background-image: url("../../images/portrait1.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat; }

.stack-title {
  margin-bottom: 1em; }
  @media screen and (max-width: 500px) {
    .stack-title {
      width: 85%;
      margin: 0 auto;
      margin-bottom: 1em; } }

.about-stack {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-row-gap: 1em; }
  @media screen and (max-width: 500px) {
    .about-stack {
      grid-template-columns: 1fr 1fr 1fr; } }
  @media screen and (min-width: 501px) and (max-width: 768px) {
    .about-stack {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr; } }

.ul-about {
  width: 100%;
  text-align: center; }
  .ul-about div {
    margin: 0em 1em 1em 1em;
    width: 30%;
    color: #edf2f4;
    margin: 0 auto;
    background-color: #000000; }
    @media screen and (max-width: 768px) {
      .ul-about div {
        font-size: 12em; } }

.react-logo {
  display: block;
  margin: 0 auto;
  -webkit-animation: spin infinite 20s linear;
          animation: spin infinite 20s linear;
  width: 5em;
  height: 5em; }

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }
  @media screen and (max-width: 768px) {
    .react-logo {
      height: 4em;
      width: 4em; } }

.logo {
  display: block;
  margin: 0 auto;
  height: 5em;
  width: 5em; }
  @media screen and (max-width: 768px) {
    .logo {
      height: 4em;
      width: 4em; } }

.png-logo {
  display: block;
  margin: 0 auto;
  height: 4em;
  width: 4em; }
  @media screen and (max-width: 768px) {
    .png-logo {
      height: 3em;
      width: 3em; } }

#react {
  text-decoration: none;
  color: #40e0d0; }

.fa-database {
  display: block;
  font-size: 4em;
  color: #f9f9f9;
  padding-bottom: .2em; }
  @media screen and (max-width: 768px) {
    .fa-database {
      font-size: 3em; } }



